

import CostItem from "./CostItem";
import './CostList.css';
import { supabase } from '../supabaseClient'; //  supabase    
import { useState,useEffect } from "react";

const CostList = (props) => {
  const [userId, setUserId] = useState(null);

  //   
  useEffect(() => {
    const getUser = async () => {
      const user = await supabase.auth.getUser(); //  
      setUserId(user?.data?.user?.id); //  user_id
    };
    getUser();
  }, []); //        

  //    user_id
  const filteredCosts = props.costs.filter(cost => cost.user_id === userId);

  const deleteCostHandler = (costId) => {
    props.onDeleteCost(costId);
  };

  if (filteredCosts.length === 0) {
    return <h2 className="cost-list__fallback">No data available</h2>; //    
  }

  return (
    <ul className="cost-list">
      {filteredCosts.map((cost) => (
       
<CostItem
  key={cost.id}
  date={cost.date}
description={cost.description}
  price={cost.price}
  category={cost.category}  
  id={cost.id}
  onDelete={deleteCostHandler}
/>
      ))}
    </ul>
  );
};

export default CostList;
